





















import { Component, Ref, Vue } from 'vue-property-decorator';
import banner from '@/components/Banner.vue';
import * as commonTypes from '@/types/common';
import { IInputInfo } from '@/types/interface';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
  },
})

export default class ErrorReport extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;

  inputList = [commonTypes.INPUT_CK_QUZ_ID, commonTypes.INPUT_EMAIL,
    commonTypes.INPUT_CK_CONSULT];

  close() {
    this.$router.push('/test/solution_page');
  }

  confirm() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    console.log(info);
    if (info) this.$router.push('/test/solution_page');
  }
}
